#header {
    position: fixed;
    width: 230px;
    border-radius: 30px;
    left: -3px;    
    top:120px;
  }

  h5{
    margin-top: 0px;
    padding: 20px;
  }
 
  #header .closemenu {
    color: rgb(0, 0, 0);
    position:fixed;
    left: 0px;
    top: 69px;
    line-height: 0px;
    border-radius: 50%;
    text-decoration: none;
    font-weight: bold;
    font-size: 28px;
    cursor: pointer;
    padding: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;    border-radius: 9px;
  
  }

  #header .pro-sidebar {
    width: 80px;
    min-width: 100%;
    max-height: 100%;
    position:sticky;
    float: left;
    color: #000;
    margin-top: 20px;
    background-color: rgb(255, 255, 255);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;    border-radius: 9px;
  }

  #header .pro-sidebar.collapsed {
    width: 31%;
    min-width: 30.4%;
    color: #000;
    text-decoration: none;
  }
  #header .pro-sidebar-inner {
    background-color: rgba(255, 255, 255, 0);
  }
  #header .pro-sidebar-inner .pro-sidebar-layout {
    overflow-y: hidden;
    text-decoration: none;
    padding-left: 0px;
    margin-right: 0px;
  }
 
  #header .pro-sidebar-inner .pro-sidebar-layout ul .pro-inner-item {
    color: rgb(2, 2, 2);
    text-decoration: none;
    font-weight: bold;
  }
  #header .pro-sidebar-layout .pro-icon-wrapper {
    background-color: rgb(255, 255, 255);
    color: rgb(14, 13, 13);
    border-radius: 50px;
  }

  .one{
    margin-top: 7px;
  }
  .two{
    margin-top: 16px;
  }
  
  .two:hover{
    background-image: linear-gradient( #3700ffdc , #0044ff 120%);
    border-radius: 9px;
    border-radius: 5px;
    transform: scale(1.09);
    transition: 0.3s;
    color: white;
  }
  .one:hover{
    background-image: linear-gradient( #3700ff , #0044ff 120%);
    border-radius: 5px;
    border-radius: 5px;
    transform: scale(1.09);
    transition: 0.3s;
    color: rgb(7, 7, 7);
  }

  #header .pro-sidebar-inner .pro-sidebar-layout .active {
    background-image:  linear-gradient( #3700ff , #0044ff 120%);
    border-radius: 6px;
    color: rgb(255, 255, 255);
    font-size: 17px;
  }
  
  #header .logo {
    padding: 0px;
  }
  
  .dark-mode-btn{
   border: none;
   background-color:rgb(255, 255, 255);
   margin: 10px;
   color: #000000;
   border-radius: 50%;
   padding-top: 3px;
   padding: 10px 10px 8px 10px;
  }

  .container{
    margin: 45px 75px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
    width: 30%;
    text-align: center; 
    border: #030303 0.2px solid;
  }
  .ownbutton{
    font-weight:600;
    color: #0044ff;
    border: rgb(255, 255, 255) 1px solid;
    border-radius: 29px;
    padding: 5px 12px;
    margin-bottom: 10px;
  }

  .ownbutton:hover{
    color: #fffefe;
    background-color: #0044ff;
    transition: .2s;
  }


  .home {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .homebox {
    text-align: left;
    border: blue solid 3px;
    border-radius: 10px;
    margin-bottom: 13px;
    margin-top: 22px;
    width: 18rem;
    padding: 17px;
    margin-left: 9%;
  }

  .buildimg{
    display: flex;
    margin: 0px;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .searchbar{
   margin-top: 100px;
   margin-left: 40%;
   align-items: center;
   align-content: center;
   text-align: center;
   border: rgb(17, 0, 255) 3px solid;
   border-radius: 7px;
   width: 28%;
   padding: 4px;
  }

.zoom{
  zoom:0fr;
}
  .themes{
    display: inline;
    font-weight: 600;
  }

.dark-mode-btn:hover{
  background-color: #0044ff;
  color: white;
}

  .dark {
    color: #131212;
    background-color: #0048ff;
    }

  @media only screen and (max-width: 70px) {
    html {
      overflow: hidden;
    }
  }

  @keyframes showMenu {
    from {
      left: -100%;
    }
    to {
      left: 0%;
    }
  }

  #header .closemenu:hover{
    transform: none;
    color: #000000;
  }
  .link{
    color: #000;
  }
  #header .pro-sidebar .pro-menu a:hover {
    color: #ffffff;
  }

  #header .pro-sidebar .pro-menu a{
    color: #110000;
  }