.App-logo {
  height: 46px;
  pointer-events: none;
  border-radius: 50%;
  position: absolute;
  margin: 0px;
  padding: 0px;
  top: 0px;
  left: 7px;
  margin-top: 10px;
  margin-left: 5px;
  background-color: rgb(236, 237, 241);
}

.tlet{
  margin-left: -2px;
  font-size: 27px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
b{
  color: rgb(118, 241, 3);
  font-size: 25px;
  font-weight: bold;
  letter-spacing: -1px;
}
.App-header {  
  height: 67px;
  margin:0px;
  color: white;
  position: fixed;
  width: 100%;
  flex-direction: column;
  align-items: center;
  background-color: rgb(4, 29, 252);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(190deg);
  }
  to {
    transform: rotate(0deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin 3s ;
    animation-timing-function: linear;
	  animation-iteration-count: 1;
    position: fixed;
    z-index: 1;
  }
}

h4{
  margin: 0px;
  margin-left: 80px;
  padding-top: 14px;
  font-family: 'Fuzzy Bubbles', cursive;
  font-size: 20px;
}

.logo-back{
  background-color: rgb(157, 252, 4);;
  height: 94px;
  width: 92px;
  position: absolute;
  top: 0px;
  border-bottom-right-radius: 53%;
  border-top-right-radius: 10%;
}